.footer {
  &__top {
    background: var(--accent);
    padding-block: 45px;
    border-radius: 20px;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 360px;
    gap: 20px;

    @media (max-width: 992px) {
      display: flex;
      flex-direction: column-reverse;
    }
  }

  &__left {
    border-radius: 20px;
    background: var(--white);
    padding: 20px;
    display: flex;
    flex-direction: column;

    .list {
      display: flex;
      flex-direction: column;
      gap: 10px;

      li {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 12px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: #3b3b3b;
      }
    }
  }

  &__heading {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    flex-wrap: wrap;
    margin-bottom: 40px;
    .logo {
      display: flex;
      align-items: center;
      gap: 20px;

      span {
        font-family: var(--second-family);
        font-weight: 500;
        font-size: 12px;
        line-height: 130%;
        letter-spacing: -0.02em;
        color: #2c2c2c;
        max-width: 265px;
      }
    }
  }

  &__row {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 40px;

    @media (max-width: 992px) {
      grid-template-columns: 1fr 1fr;
    }
  }

  &__menu {
    &-title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 18px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: #5a86fd;
      margin-bottom: 25px;
    }

    .list {
      display: flex;
      flex-direction: column;
      gap: 17px;

      a {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 14px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: #3b3b3b;
        transition: var(--transition);

        @media (any-hover: hover) {
          &:hover {
            color: var(--accent);
          }
        }
      }
    }
  }

  &__right {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  &__catalog {
    background: var(--white);
    color: var(--gray);
    justify-content: space-between;
    padding-inline: 40px;
    @media (any-hover: hover) {
      &:hover {
        background: var(--hovered);
        color: var(--white);
        svg path {
          fill: var(--white);
        }
      }
    }
  }

  &__socials {
    display: flex;
    flex-direction: column;
    gap: 14px;
    background: var(--white);
    padding: 28px;
    border-radius: 20px;
    a {
      font-family: var(--second-family);
      font-weight: 700;
      font-size: 18px;
      line-height: 110%;
      letter-spacing: -0.02em;
      color: var(--gray);
      transition: color var(--transition);
      display: flex;
      align-items: center;
      gap: 12px;

      .icon-wrapper {
        width: 32px;
        height: 32px;
        display: grid;
        place-items: center;
        background: var(--blue);
        border-radius: 100%;
        transition: background-color var(--transition);
      }
      .icon {
        fill: var(--accent);
        transition: var(--transition);
      }

      @media (any-hover: hover) {
        &:hover {
          color: var(--accent);
          .icon-wrapper {
            background: var(--accent);
          }
          .icon {
            fill: var(--white);
          }
        }
      }
    }
  }

  &__subscribe {
    display: flex;
    flex-direction: column;
    gap: 11px;

    background: var(--white);
    padding: 28px;
    border-radius: 20px;
    p {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: #3b3b3b;
      margin: 0;
    }
    &-btn {
      background-color: var(--accent);
      color: var(--white);
      padding-block: 12px;

      @media (any-hover: hover) {
        &:hover {
          background: var(--hovered);
        }
      }
      &:active {
        background: var(--active);
      }
    }
  }

  &__bottom {
    background: var(--white);
    padding-block: 22px;

    &-row {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 10px;
      flex-wrap: wrap;
    }

    a,
    p {
      font-family: var(--second-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 110%;
      letter-spacing: -0.05em;
      color: #000;

      span {
        font-weight: bold;
      }
    }

    a {
      transition: color var(--transition);

      @media (any-hover: hover) {
        &:hover {
          color: var(--accent);
        }
      }
    }
  }
}
