.f-block {
  margin-bottom: 100px;
  &__header {
    background: url("../../img/f-bg.webp") center / cover no-repeat;
    padding: 20px 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    flex-wrap: wrap;
    margin-bottom: 22px;
    border-radius: clamp(10px, 2vw, 20px);
  }

  &__title {
    color: var(--white);
  }

  &__link {
    background: var(--white);
    color: var(--gray);
    max-width: 300px;
    width: 100%;
    @media (any-hover: hover) {
      &:hover {
        background: var(--hovered);
        color: var(--white);
      }
    }

    &:active {
      background: var(--active);
      color: var(--white);
    }
  }

  &__content {
    display: grid;
    grid-template-columns: 1fr 363px;
    gap: 16px;
    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
    .left {
      border-radius: 20px;
      background: var(--blue);
      padding: 23px;
    }
    .items {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 20px;

      @media (max-width: 575px) {
        grid-template-columns: 1fr 1fr;
      }
    }
    .item {
      display: flex;
      align-items: center;
      gap: 10px;
      max-width: fit-content;
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: #272727;

      border-radius: 100px;
      padding: 10px 22px;
      background: var(--white);

      svg {
        fill: var(--accent);
      }
      @media (max-width: 575px) {
        padding: 0;
        background: transparent;
      }
    }

    .right {
      background: url("../../img/bg-contr.webp") center / cover no-repeat;
      border-radius: 20px;
      padding: 20px 29px;
      display: flex;
      flex-direction: column;
      .subtitle {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: clamp(18px, 2.5vw, 22px);
        line-height: 97%;
        letter-spacing: -0.01em;
        text-align: center;
        color: var(--accent);
        margin-bottom: 126px;
        flex: 1 1 auto;
      }
    }
  }

  &__btn {
    color: var(--white);
    background: var(--accent);
    padding-block: 13px;
    @media (any-hover: hover) {
      &:hover {
        background: var(--hovered);
        color: var(--white);
      }
    }

    &:active {
      background: var(--active);
    }
  }

  .wrapper-button {
    position: relative;

    &:before {
      content: "";

      position: absolute;
      top: 50%;
      left: 0;
      width: 50%;
      height: 1px;
      transform: translateY(-50%);
      background: var(--accent);
    }

    &:after {
      content: "";

      position: absolute;
      top: 50%;
      right: 0;
      width: 50%;
      height: 1px;
      transform: translateY(-50%);
      background: var(--accent);
    }
  }

  .btn-more {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 97%;
    letter-spacing: -0.01em;
    color: var(--accent);
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    max-width: fit-content;
    margin: 0 auto;
    margin-top: 25px;
    background: var(--blue);
    transition: color 0.3s ease;
    position: relative;
    z-index: 1;
    padding-inline: 30px;
    @media (any-hover: hover) {
      &:hover {
        color: var(--hovered);
      }
    }
  }
}
