.callback {
  border-radius: 20px;
  background: var(--accent);
  padding-top: 41px;
  margin-bottom: 100px;
  &__inner {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    @media (max-width: 992px) {
      flex-direction: column;
    }
  }

  &__left {
    max-width: 630px;
    display: flex;
    flex-direction: column;
  }

  &__title {
    color: var(--white);
    margin-bottom: 15px;
  }

  &__desc {
    color: var(--white);
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 136%;
    letter-spacing: -0.02em;
    margin-bottom: 23px;
  }

  &__image {
    img {
      display: block;
    }

    @media (max-width: 992px) {
      transform: translateX(20px);
    }
  }

  &__form {
    margin-bottom: 42px;
    border-radius: 30px;
    background: var(--white);
    padding: 25px;
    max-width: 460px;
    @media (max-width: 992px) {
      max-width: 100%;
    }
    &-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 20px;
      margin-bottom: 35px;
      p {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 18px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--gray);
        margin: 0;
      }

      img {
        max-width: 51px;
      }
    }
  }

  .form-cb {
    display: flex;
    flex-direction: column;
    gap: 14px;

    .input {
      padding-block: 16px;
    }

    .policy {
      margin-top: 14px;
      position: relative;

      input {
        position: absolute;
        inset: 0;
        z-index: 1;
        cursor: pointer;
        opacity: 0;
        appearance: none;
      }

      label {
        position: relative;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        line-height: 136%;
        letter-spacing: -0.02em;
        color: var(--gray);
        display: block;
        padding-left: 28px;
        &:before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          border: 1px solid #c1c1c1;
          width: 17px;
          height: 17px;
          display: grid;
          place-items: center;
          transition: var(--transition);
        }
      }

      input:checked + label:before {
        content: "";
        color: var(--white);
        background: var(--accent);
        border-color: transparent;
      }
    }

    &__btn {
      background: var(--accent);
      color: var(--white);
      padding-block: 17px;
      @media (any-hover: hover) {
        &:hover {
          background: var(--hovered);
        }
      }

      &:active {
        background: var(--active);
      }
    }
  }

  &__socials {
    background: var(--white);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    padding: 20px 25px;
    max-width: 420px;
    margin-top: 25px;
    position: relative;

    &:after {
      content: "";
      position: absolute;
      bottom: -3px;
      right: -19px;
      width: 45px;
      height: 30px;
      background: url("../../img/icons/message.svg") no-repeat;
    }
    .mail {
      display: flex;
      flex-direction: column;
      gap: 8px;
      margin-bottom: 20px;
      span {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 14px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--gray);
      }
      p {
        margin: 0;
        display: flex;
        align-items: center;
        gap: 5px;
        font-family: var(--second-family);
        font-weight: 700;
        font-size: 22px;
        line-height: 110%;
        letter-spacing: -0.02em;
        color: var(--gray);
        transition: color var(--transition);
      }

      @media (any-hover: hover) {
        &:hover {
          p {
            color: var(--accent);
          }
        }
      }
    }

    .socals {
      display: flex;
      align-items: center;
      gap: 20px;

      a {
        display: flex;
        align-items: center;
        gap: 5px;
        font-family: var(--second-family);
        font-weight: 700;
        font-size: 22px;
        line-height: 110%;
        letter-spacing: -0.02em;
        color: var(--accent);
        transition: var(--transition);
        will-change: opacity;

        &.whatsapp {
          color: #41a469;
        }

        @media (any-hover: hover) {
          &:hover {
            opacity: 0.7;
          }
        }
      }
    }
  }
}
