.hero {
  padding-top: 90px;
  position: relative;
  padding-bottom: 240px;

  @media (max-width: 640px) {
    padding-bottom: 50px;
    padding-top: 200px;
  }
  &__bg {
    position: absolute;
    z-index: -1;
    top: -350px;
    left: 60px;
    width: 100%;
    height: auto;
    img {
      width: 100%;
      height: 100%;
    }

    @media (max-width: 1199px) {
      top: -50px;
    }

    @media (max-width: 767px) {
      top: -100px;
      left: 0;
      height: 100%;
    }
  }

  &__content {
    position: relative;
    z-index: 1;
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: clamp(35px, 4vw, 50px);
    line-height: 97%;
    letter-spacing: -0.01em;
    text-align: center;
    color: var(--white);
    max-width: 750px;
    margin: 0 auto;
    margin-bottom: 28px;
  }

  &__desc {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 97%;
    letter-spacing: -0.01em;
    text-align: center;
    color: #fff;
  }

  &__filter {
    margin-bottom: 10px;
  }

  &__feats {
    display: flex;
    flex-wrap: wrap;
    backdrop-filter: blur(4px);
    background: rgba(245, 245, 245, 0.5);
    border-radius: 20px;
    padding: 24px 29px;
    gap: 14px;
  }

  &__feat {
    border-radius: 100px;
    padding: 10px 22px;
    background: var(--white);
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 97%;
    letter-spacing: -0.01em;
    color: var(--gray);

    &.active {
      background: var(--blue);
      color: var(--accent);
    }
  }
}

.filter-hero {
  background: var(--white);
  padding: 26px 20px;
  border-radius: 20px;

  &__options {
    display: flex;
    flex-wrap: wrap;
    gap: 13px;
    margin-bottom: 18px;
    @media (max-width: 575px) {
      flex-direction: column;
    }
    .option {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 10px;
      background: var(--blue);
      position: relative;
      border-radius: 100px;
      padding: 10px 22px;
      transition: background-color 0.3s ease;
      input {
        position: absolute;
        z-index: 1;
        inset: 0;
        cursor: pointer;
        opacity: 0;
        appearance: none;
      }
      .icon {
        fill: var(--accent);
      }
      label {
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 14px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--accent);
        transition: color 0.3s ease;
      }

      &:has(input:checked) {
        background: var(--accent);

        label {
          color: var(--white);
        }

        .icon {
          fill: var(--white);
        }
      }
    }
  }

  &__fields {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 18px;

    @media (max-width: 992px) {
      grid-template-columns: 1fr 1fr;
    }

    @media (max-width: 575px) {
      grid-template-columns: 1fr;
    }
  }
  &__btn {
    background: var(--violet);
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 97%;
    letter-spacing: -0.01em;
    text-align: center;
    color: #2e2e2e;
    padding: 23px 20px;
    border-radius: 10px;
    transition: var(--transition);
    @media (any-hover: hover) {
      &:hover {
        color: var(--white);
        background: var(--hovered);
      }
    }
    &:active {
      color: var(--white);
      background: var(--active);
    }

    @media (max-width: 992px) {
      grid-column: span 2;
    }
    @media (max-width: 575px) {
      grid-column: auto;
    }
  }
  .custom-select {
    border-radius: 10px;
    padding: 23px 16px;
    background: #f5f5f5;
    border: none;
    .ss-single {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: #272727;
    }
  }
}
