.offer {
  background: #f8f8f8;
  border-radius: 20px;
  padding-block: 70px;
  margin-bottom: 100px;
  @media (max-width: 992px) {
    padding-block: 50px;
  }
  &__title {
    color: var(--title-color);
    margin-bottom: 40px;
  }

  .offer__items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;

    @media (max-width: 1140px) {
      grid-template-columns: 1fr 1fr;
    }
  }
}
