.question {
  background: #f8f8f8;
  padding-block: 30px;
  margin-bottom: 100px;

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: clamp(20px, 4vw, 65px);

    @media (max-width: 992px) {
      display: flex;
      flex-direction: column-reverse;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 20px;
    p {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 16px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: #272727;
      height: 100%;
      flex: 1 1 auto;
      margin: 0;
    }
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 35px;
    line-height: 97%;
    letter-spacing: -0.01em;
    margin: 0;
    color: #afafaf;
    margin-bottom: 10px;
    span {
      color: var(--gray);
    }
  }

  &__btn {
    background: var(--accent);
    color: var(--white);
    max-width: 305px;
    padding-block: 17px;
    @media (any-hover: hover) {
      &:hover {
        background: var(--hovered);
      }
    }
    &:active {
      background: var(--active);
    }
  }
}
