}
.faq--buying {
  background: #f8f8f8;
  padding-block: 52px;
  border-radius: clamp(10px, 2vw, 20px);
}

.auth-block {
  margin-bottom: 100px;
}
@media (max-width: 992px) {
  .auth-block {
    margin-top: 60px;
  }
}
.auth-block .auth-form {
  border-radius: 20px;
  max-width: 486px;
  width: 100%;
  margin: 0 auto;
  background: #f8f8f8;
  padding-block: 60px;
  padding-inline: clamp(15px, 3vw, 30px);
  position: relative;
}
.auth-block .auth-form__actions {
  position: absolute;
  top: -40px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 10px;
}
@media (max-width: 767px) {
  .auth-block .auth-form__actions {
    left: 0;
  }
}
.auth-block .auth-form__actions .btn {
  background: var(--blue);
  color: var(--accent);
  display: flex;
  align-items: center;
  gap: 10px;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 97%;
  letter-spacing: -0.01em;
}
.auth-block .auth-form__actions .btn svg {
  max-width: 16px;
  height: 16px;
  fill: var(--accent);
}
@media (any-hover: hover) {
  .auth-block .auth-form__actions .btn:hover {
    background: var(--accent);
    color: var(--white);
  }
  .auth-block .auth-form__actions .btn:hover svg {
    fill: var(--white);
  }
}
.auth-block .auth-form__actions .btn.active {
  background: var(--accent);
  color: var(--white);
}
.auth-block .auth-form__actions .btn.active svg {
  fill: var(--white);
}
.auth-block .auth-form__title {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: clamp(18px, 3vw, 25px);
  line-height: 97%;
  letter-spacing: -0.01em;
  color: var(--gray);
  margin-bottom: 30px;
}
.auth-block .form-auth {
  display: flex;
  flex-direction: column;
  gap: 13px;
}
.auth-block .form-auth__btn {
  background: var(--accent);
  color: var(--white);
}
@media (any-hover: hover) {
  .auth-block .form-auth__btn:hover {
    background: var(--hovered);
  }
}
.auth-block .form-auth .policy {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 12px;
  line-height: 97%;
  letter-spacing: -0.01em;
  color: var(--gray);
  position: relative;
  padding-left: 20px;
  margin-bottom: 22px;
}
.auth-block .form-auth .policy a {
  color: inherit;
  text-decoration: underline;
  text-decoration-skip-ink: none;
}
.auth-block .form-auth .policy::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  background: url("../img/icons/unlock.svg") no-repeat;
}
.auth-block .auth-google {
  background: #c82f2f;
  border-radius: 5px;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 97%;
  letter-spacing: -0.01em;
  color: #fff;
  text-align: left;
  position: relative;
  justify-content: start;
  padding-left: 52px;
}
@media (any-hover: hover) {
  .auth-block .auth-google:hover {
    background: #8d2020;
  }
}
.auth-block .auth-google:before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 11px;
  width: 32px;
  height: 32px;
  border-radius: 5px;
  background: #fff url("../img/icons/google-logo.png") no-repeat;
}