html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-height: 100%;
  font-size: 16px;
  overflow-x: hidden;
  background-color: var(--white);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.hidden-sm {
  @media (max-width: 768px) {
    display: none !important;
  }
}

.input {
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background: #f7f7f7;
  font-family: var(--second-family);
  font-weight: 600;
  font-size: 12px;
  line-height: 115%;
  letter-spacing: -0.02em;
  color: #212121;
  width: 100%;
  outline: none;
  padding: 10px 10px 10px 10px;
  transition: 0.3s ease;
  &::placeholder {
    opacity: 0.2;
  }

  @media (any-hover: hover) {
    &:hover {
      box-shadow: 0 0 0 3px rgba(113, 48, 142, 0.12);
      border-color: var(--accent);
    }
  }

  &:focus {
    box-shadow: 0 0 0 3px rgba(113, 48, 142, 0.12);
    border-color: var(--accent);
  }
}

.icon {
  max-width: 16px;
  height: 16px;
}

.icon-wrapper {
  width: 33px;
  height: 33px;
  display: grid;
  place-items: center;
  border-radius: 100%;
  background: #e6eefb;
  transition: var(--transition);
  @media (any-hover: hover) {
    &:hover {
      background: var(--accent);

      svg {
        fill: var(--white);
      }
    }
  }
}

.ss-list {
  border-radius: 10px;
  padding: 23px 16px;
  background: #f5f5f5;
}

.ss-content {
  border-radius: 10px;
}

.ss-content.ss-open-above {
  border: none;
  background: transparent;
}

.title__section {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: clamp(35px, 3.5vw, 45px);
  line-height: 97%;
  letter-spacing: -0.01em;
  margin: 0;
}

.btn {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 97%;
  letter-spacing: -0.01em;
  text-align: center;
  color: #3e3e3e;
  padding: 23px 20px;
  transition: var(--transition);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  text-align: center;
}

.cart {
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  padding: 9px 9px 30px 9px;
  background: var(--white);
  border: 1px solid var(--gray);
  gap: 20px;

  &__image {
    position: relative;
    padding: 33%;
    img {
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      object-fit: cover;
      border-radius: 10px;
    }
  }

  &__project {
    display: grid;
    grid-template-columns: 48px 1fr;
    align-items: center;
    gap: 15px;
    margin-bottom: 7px;
    flex: 1 1 auto;
    .info {
      display: flex;
      flex-direction: column;
      gap: 7px;

      a {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 18px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: var(--gray);
      }

      p {
        margin: 0;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 12px;
        line-height: 97%;
        letter-spacing: -0.01em;
        color: #272727;
        opacity: 0.5;
      }
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 25px;

    &-action {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 40px;

      .actions {
        display: flex;
        align-items: center;
        gap: 5px;

        &__btn {
          width: 34px;
          height: 34px;
          display: grid;
          place-items: center;
          border-radius: 100%;
          background: var(--blue);
          svg {
            width: 18px;
            height: 18px;
            fill: var(--white);
            transition: 0.3s ease;
          }

          @media (any-hover: hover) {
            &:hover {
              svg {
                fill: var(--accent);
              }
            }
          }
        }
      }
    }
  }

  &__text {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 97%;
    letter-spacing: -0.01em;
    color: #27272765;
    margin: 0;
    display: flex;
    align-items: center;
    gap: 14px;
    flex-wrap: wrap;
    span {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 18px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: #272727;
    }
  }
}

.swiper-button-lock {
  display: none !important;
}

.no-fill svg {
  fill: none !important;
  stroke: var(--accent);
}

section {
  @media (max-width: 992px) {
    margin-bottom: 60px;
  }
}
