.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  // display: none;
  &__top {
    padding-block: 11px;
    background: var(--3d3d3d);

    &-wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 20px;
    }
  }

  &__nav {
    flex: 1 1 auto;
    max-width: 715px;
    .list {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 10px;
    }

    &-link {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 12px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--white);
      transition: color 0.2s ease;
      @media (any-hover: hover) {
        &:hover {
          color: var(--accent);
        }
      }
    }
  }

  &__bottom {
    padding-top: 11px;
    &-wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 20px;
    }
  }

  .logo {
    display: flex;
    align-items: center;
    gap: 5px;
    max-width: 200px;
    width: 100%;
    img {
      max-width: 86px;
    }
    span {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 10px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--3d3d3d);
    }
  }

  &__search {
    max-width: 570px;
    width: 40vw;
    position: relative;

    @media (max-width: 1200px) {
    }
    &:before {
      content: "";
      position: absolute;
      top: 45%;
      transform: translateY(-50%);
      left: 10px;
      width: 13px;
      height: 13px;
      background: url("../../img/svg/search.svg") center / cover no-repeat;
    }
    .input {
      padding: 10px 90px 10px 40px;
    }

    &-btn {
      position: absolute;
      top: 5px;
      right: 5px;
      font-family: var(--second-family);
      font-weight: 600;
      font-size: 12px;
      line-height: 115%;
      letter-spacing: -0.02em;
      color: var(--white);
      border-radius: 5px;
      padding: 5px 20px;
      background: var(--accent);
      transition: var(--transition);
      @media (any-hover: hover) {
        &:hover {
          background: var(--hovered);
        }
      }

      &:active {
        background: var(--active);
      }
    }
  }

  &__actions {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 9px 14px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 97%;
    letter-spacing: -0.01em;
    text-align: center;
    transition: var(--transition);
    border-radius: 5px;

    &.btn--gray {
      color: #1a1a1a;
      background: var(--white);
      @media (any-hover: hover) {
        &:hover {
          background: var(--hovered);
          color: var(--white);
        }
      }

      &:active {
        background: var(--active);
      }
    }
    &.btn--accent {
      color: var(--white);
      background: var(--accent);
      @media (any-hover: hover) {
        &:hover {
          background: var(--hovered);
        }
      }

      &:active {
        background: var(--active);
      }
      &:before {
        content: "";
        width: 14px;
        height: 14px;
        background: url("../../img/svg/plus.svg") center / cover no-repeat;
      }
    }
  }
}

.auth {
  display: flex;
  gap: clamp(20px, 3vw, 40px);
  align-items: center;

  &__link {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 97%;
    letter-spacing: -0.01em;
    color: var(--white);
    transition: color 0.2s ease;
    @media (any-hover: hover) {
      &:hover {
        color: var(--accent);
      }
    }
  }
}
