.through {
  margin-bottom: 100px;

  &__title {
    color: var(--gray);
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    flex-wrap: wrap;
    margin-bottom: 40px;
  }

  &__more {
    padding-block: 17px;
    border: 1px solid #bbb;
    max-width: 220px;
    width: 100%;

    @media (any-hover: hover) {
      &:hover {
        background: var(--accent);
        color: var(--white);
        border-color: transparent;
      }
    }

    &:active {
      background: var(--active);
      color: var(--white);
    }
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;

    @media (max-width: 992px) {
      grid-template-columns: 1fr 1fr;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    padding: 18px 13px;
    background: #f8f8f8;

    .img {
      position: relative;
      padding: 33%;
      margin-bottom: 18px;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 10px;
      }
    }

    .top {
      display: flex;
      flex-wrap: wrap;
      gap: 7px;
      margin-bottom: 12px;
      &-item {
        border-radius: 100px;
        padding: 5px 9px;
        display: flex;
        align-items: center;
        gap: 8px;
        background: #eaf1ff;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        line-height: 136%;
        letter-spacing: -0.02em;
        color: var(--siniy);

        .icon {
          width: 13px;
          height: 13px;

          fill: var(--accent);
        }
      }
    }

    .title-link {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 16px;
      line-height: 136%;
      letter-spacing: -0.02em;
      color: var(--tekst);
      display: block;
      margin-bottom: 12px;
      transition: color 0.3s ease;

      @media (any-hover: hover) {
        &:hover {
          color: var(--hovered);
        }
      }
    }

    .excerpt {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 12px;
      line-height: 136%;
      letter-spacing: -0.02em;
      color: #2d2d2d;
      opacity: 0.38;
    }
  }
}
