.uprove {
  margin-bottom: 100px;
  &__inner {
    display: grid;
    grid-template-columns: 400px 295px 460px;
    gap: 20px;

    @media (max-width: 1200px) {
      grid-template-columns: 1fr 1fr;
    }

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  &__title {
    margin-bottom: 40px;
    span {
      color: #afafaf;
    }
  }

  &__subtitle {
    display: flex;
    align-items: center;
    gap: 7px;
    border-radius: 100px;
    padding: 10px 22px;
    background: var(--blue);
    max-width: fit-content;
    margin-top: auto;
  }

  &__col {
    position: relative;
    height: 100%;
  }

  &__image {
    position: absolute;
    top: -50px;
    left: 0;
    width: 100%;
    height: 100%;

    @media (max-width: 1200px) {
      position: static;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    border-radius: 20px;
    padding: 24px;
    background: #f8f8f8;
    &-subtitle {
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 21px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--gray);
      margin-bottom: 15px;
    }
    &-description {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 136%;
      letter-spacing: -0.02em;
      color: #2d2d2d;
      opacity: 0.38;
      flex: 1 1 auto;
      margin-bottom: 40px;
    }

    &-block {
      display: grid;
      grid-template-columns: 87px 1fr;
      gap: 10px;
      align-items: center;
      a {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        line-height: 136%;
        letter-spacing: -0.02em;
        text-decoration: underline;
        text-decoration-skip-ink: none;
        color: #2d2d2d;
        transition: color 0.3s ease;

        @media (any-hover: hover) {
          &:hover {
            color: var(--accent);
          }
        }
      }
    }
  }
}
