@forward "vars";
@forward "mixins";
@forward "fonts";
@forward "settings";
@forward "fancybox";

@use "./components/header";
@use "./components/hero";
@use "./components/franchises";
@use "./components/offer";
@use "./components/question";
@use "./components/through-block";
@use "./components/uprove";
@use "./components/callback";
@use "./pages/catalog-page";
@use "./pages/buss-page";
@use "./pages/blog-page";
@use "./pages/popular-page";
@use "./pages/favorite-page";
@use "./pages/about-page";
@use "./pages/compare";
@use "./pages/buying";
@use "./pages/sell";
@use "./components/footer";

.breadcrumbs {
  margin-bottom: 40px;

  ul {
    display: flex;
    align-items: center;
    gap: 6px;

    li {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: #9ebefe;
      position: relative;

      &:not(:last-child) {
        &:after {
          content: "/";
          font-family: var(--font-family);
          font-weight: 500;
          font-size: 14px;
          line-height: 97%;
          letter-spacing: -0.01em;
          color: #9ebefe;
        }
      }
      &:last-child {
        color: var(--accent);
      }
      a {
        color: inherit;
        font-size: inherit;
      }
    }
  }
}

.faq {
  margin-bottom: 100px;

  &__title {
    margin-bottom: 40px;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    align-items: flex-start;
  }

  &__item {
    padding: 17px 25px;
    border-radius: 10px;
    background: var(--blue);
    cursor: pointer;
    & > * {
      pointer-events: none;
    }
    &-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 20px;
    }

    &-title {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 18px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--accent);
    }
    &-toggler {
      transition: var(--transition);
    }

    &-footer {
      display: grid;
      grid-template-rows: 0fr;
      transition: var(--transition);
      overflow: hidden;
    }
    &-content {
      overflow: hidden;
    }
  }

  .faq__item--active {
    .faq__item-toggler {
      transform: rotate(180deg);
    }
    .faq__item-footer {
      grid-template-rows: 1fr;
      margin-top: 20px;
    }
  }

  &--buying {
    background: #f8f8f8;
    padding-block: 52px;
    border-radius: clamp(10px, 2vw, 20px);
  }
}

.auth-block {
  margin-bottom: 100px;

  @media (max-width: 992px) {
    margin-top: 60px;
  }
  .auth-form {
    border-radius: 20px;
    max-width: 486px;
    width: 100%;
    margin: 0 auto;
    background: #f8f8f8;
    padding-block: 60px;
    padding-inline: clamp(15px, 3vw, 30px);
    position: relative;
    &__actions {
      position: absolute;
      top: -40px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 10px;
      @media (max-width: 767px) {
        left: 0;
      }
      .btn {
        background: var(--blue);
        color: var(--accent);
        display: flex;
        align-items: center;
        gap: 10px;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 14px;
        line-height: 97%;
        letter-spacing: -0.01em;
        svg {
          max-width: 16px;
          height: 16px;
          fill: var(--accent);
        }

        @media (any-hover: hover) {
          &:hover {
            background: var(--accent);
            color: var(--white);
            svg {
              fill: var(--white);
            }
          }
        }

        &.active {
          background: var(--accent);
          color: var(--white);
          svg {
            fill: var(--white);
          }
        }
      }
    }

    &__title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: clamp(18px, 3vw, 25px);
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--gray);
      margin-bottom: 30px;
    }
  }

  .form-auth {
    display: flex;
    flex-direction: column;
    gap: 13px;

    &__btn {
      background: var(--accent);
      color: var(--white);

      @media (any-hover: hover) {
        &:hover {
          background: var(--hovered);
        }
      }
    }

    .policy {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 12px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--gray);
      position: relative;
      padding-left: 20px;
      margin-bottom: 22px;
      a {
        color: inherit;
        text-decoration: underline;
        text-decoration-skip-ink: none;
      }

      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 15px;
        height: 15px;
        background: url("../img/icons/unlock.svg") no-repeat;
      }
    }
  }

  .auth-google {
    background: #c82f2f;
    border-radius: 5px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 97%;
    letter-spacing: -0.01em;
    color: #fff;
    text-align: left;
    position: relative;
    justify-content: start;
    padding-left: 52px;
    @media (any-hover: hover) {
      &:hover {
        background: #8d2020;
      }
    }

    &:before {
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 11px;
      width: 32px;
      height: 32px;
      border-radius: 5px;
      background: #fff url("../img/icons/google-logo.png") no-repeat;
    }
  }
}
